<template>
  <div>
    <form @submit.prevent="validateBeforeSubmit">
      <div id="leftPage" class="col-lg-7 pd-tb-mini pd-r-50">
        <img class="img-logo" src="http://www.farmable.me/_/img/logo/farmable-logo-mobile-beta.png" alt="">
        <div class="alert alert-danger" v-show="errorMsg">
          <strong>Oops!</strong> {{ errorMsg }}
          <ul>
            <li v-if="errorSub">{{ errorSub }}</li>
          </ul>
        </div>
        <div class="alert alert-success" v-show="success">
          <strong>Success!</strong> Your order has been submitted. Please wait for 5 seconds to make payment.
        </div>
        <h3 class="mr-b-20">Customer information</h3>
        <div class="row">
          <div class="form-group col-sm-6">
            <label for="" class="control-label">Name</label>
            <p class="control has-icon has-icon-right">
              <input name="name" v-model="name" v-validate.initial="'required|alpha_spaces'" class="form-control" :class="{'input': true, 'is-danger': errors.has('name') }"
                type="text" placeholder="Enter your First Name">
              <span v-show="errors.has('name')" class="help is-danger">{{ errors.first('name') }}</span>
            </p>
            <!--<input type="text" class="form-control" placeholder="Enter your First Name" v-model="name">-->
          </div>
          <div class="form-group col-sm-6">
            <label for="" class="control-label">Phone #</label>
            <p class="control has-icon has-icon-right">
              <input name="phone" v-model="phone" v-validate.initial="'required|numeric'" class="form-control" :class="{'input': true, 'is-danger': errors.has('phone') }"
                type="text" placeholder="Enter your Phone #">
              <span v-show="errors.has('phone')" class="help is-danger">{{ errors.first('phone') }}</span>
            </p>
            <!--<input type="number" class="form-control" placeholder="Enter your Phone #" v-model="phone">-->
          </div>
          <div class="form-group col-sm-6">
            <label for="" class="control-label">Email Address</label>
            <p :class="{ 'control': true }">
              <input v-validate.initial="'required|email'" class="form-control" :class="{'input': true, 'is-danger': errors.has('email') }"
                name="email" type="text" placeholder="Enter your Email Address" v-model="email">
              <span v-show="errors.has('email')" class="help is-danger">{{ errors.first('email') }}</span>
            </p>
            <!--<input type="email" class="form-control" id="" placeholder="Enter your Email Address" v-model="email">-->
          </div>
        </div>
        <hr>
        <h3 class="mr-b-20">Address / Shipping Information</h3>

        <div class="row">
          <div class="form-group col-sm-12">
            <label for="" class="control-label">Delivery Location</label>
            <!--<input type="text" class="form-control" id="" placeholder="Enter your delivery Location" v-model="location">-->
            <p class="control has-icon has-icon-right">
              <input name="location" v-model="location" v-validate.initial="'required|alpha_spaces'" class="form-control" :class="{'input': true, 'is-danger': errors.has('location') }"
                type="text" placeholder="Enter your delivery Location">
              <span v-show="errors.has('location')" class="help is-danger">{{ errors.first('location') }}</span>
            </p>
          </div>
          <div class="form-group col-sm-12">
            <label for="" class="control-label">Delivery Time</label>
            <p class="control has-icon has-icon-right">
            <input type="time" class="form-control" placeholder="Enter your delivery time" v-model="time">
              <!--<input text="time" name="time" v-model="time" v-validate.initial="'required'" class="form-control" :class="{'input': true, 'is-danger': errors.has('time') }" placeholder="Enter your delivery time">-->
              <span class="help is-danger" v-show="!time">The time field is required.</span>
            </p>
          </div>
        </div>
      </div>
      <div id="rightPage" class="col-lg-5">
        <h3 class="mr-b-20">Beef Order Form</h3>

        <div class="row sm-gutter">
          <div class="form-group col-xs-5">
            <label for="" class="control-label">Product</label>
            <input type="text" class="form-control" value="Boneless Stewing Beef" readonly>
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">Kg</label>
            <input type="number" class="form-control" min="0" v-model="boneless_quantity">
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">GH&#x20B5;</label>
            <input type="text" readonly class="form-control" placeholder="20.30" :value="boneless_quantity * fieldss.boneless_stewing_beef">
          </div>
        </div>

        <div class="row sm-gutter">
          <div class="form-group col-xs-5">
            <label for="" class="control-label">Product</label>
            <input type="text" class="form-control" value="Bone In Stewing Beef" readonly>
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">Kg</label>
            <input type="number" min="0" class="form-control" v-model="bonein_quantity">
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">GH&#x20B5;</label>
            <input type="text" readonly class="form-control" :value="bonein_quantity * fieldss.bone_in_stewing_beef">
          </div>
        </div>

        <div class="row sm-gutter">
          <div class="form-group col-xs-5">
            <label for="" class="control-label">Product</label>
            <input type="text" class="form-control" value="Ribs" readonly>
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">Kg</label>
            <input type="number" class="form-control" min="0" v-model="ribs_quantity">
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">GH&#x20B5;</label>
            <input type="text" readonly class="form-control" :value="ribs_quantity * fieldss.ribs">
          </div>
        </div>

        <div class="row sm-gutter">
          <div class="form-group col-xs-5">
            <label for="" class="control-label">Product</label>
            <input type="text" class="form-control" value="Mince" readonly>
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">Kg</label>
            <input type="number" class="form-control" min="0" v-model="mince_quantity">
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">GH&#x20B5;</label>
            <input type="text" readonly class="form-control" :value="mince_quantity * fieldss.mince">
          </div>
        </div>

        <div class="row sm-gutter">
          <div class="form-group col-xs-5">
            <label for="" class="control-label">Product</label>
            <input type="text" class="form-control" value="Steak Boneless" readonly>
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">Kg</label>
            <input type="number" class="form-control" min="0" v-model="steak_quantity">
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">GH&#x20B5;</label>
            <input type="text" readonly class="form-control" :value="steak_quantity * fieldss.steak_bonless">
          </div>
        </div>

        <div class="row sm-gutter">
          <div class="form-group col-xs-5">
            <label for="" class="control-label">Product</label>
            <input type="text" class="form-control" value="Steak Bone In" readonly>
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">Kg</label>
            <input type="number" class="form-control" min="0" v-model="steakless_quantity">
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">GH&#x20B5;</label>
            <input type="text" readonly class="form-control" :value="steakless_quantity * fieldss.steak_bone_in">
          </div>
        </div>

        <div class="row sm-gutter">
          <div class="form-group col-xs-5">
            <label for="" class="control-label">Product</label>
            <input type="text" class="form-control" value="Wele" readonly>
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">Kg</label>
            <input type="number" class="form-control" min="0" v-model="wele_quantity">
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">GH&#x20B5;</label>
            <input type="text" readonly class="form-control" :value="wele_quantity * fieldss.wele">
          </div>
        </div>

        <div class="row sm-gutter">
          <div class="form-group col-xs-5">
            <label for="" class="control-label">Product (Please Specify)</label>
            <input type="text" class="form-control" placeholder="Off-Cuts (Tail, Cheek, Foot, Etc.)" v-model="off_cut">
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">Kg</label>
            <input type="number" class="form-control" min="0" v-model="offcut_quantity">
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">GH&#x20B5;</label>
            <input type="text" readonly class="form-control" :value="offcut_quantity * fieldss.off_cut">
          </div>
        </div>

        <div class="row sm-gutter">
          <div class="form-group col-xs-5">
            <label for="" class="control-label">Product (Please Specify)</label>
            <input type="text" class="form-control" placeholder="Offal (Liver, Stomach, Intestine,etc)" v-model="offal">
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">Kg</label>
            <input type="number" class="form-control" min="0" v-model="offal_quantity">
          </div>
          <div class="form-group col-xs-3">
            <label for="" class="control-label">GH&#x20B5;</label>
            <input type="text" readonly class="form-control" :value="offal_total">
          </div>
        </div>

        <div class="row">
          <div class="col-xs-6 col-sm-4 col-sm-offset-2 pull-right">
            <h4 class="mr-t-10 fs16 bold-4">Total: GH&#x20B5; {{ total }}</h4>
          </div>
        </div>

        <hr class="mr-tb-20">

        <div class="row">
          <div class="form-group col-md-12 mr-t-20 text-center">
            <button type="submit" class="btn btn-lg btn-primary">Proceed to Payment</button>
          </div>
        </div>
        <div class="bg-gray1 pd-30 mr-t-20 rd-5">
          Farmable is a start-up social enterprise working to build the capacity of small-holder cattle farmers to produce and sell
          quality assured beef in Ghana.
        </div>
      </div>
      </form>
  </div>
</template>

<script>
  import Devless from '../utils/devless'

  export default {
    data() {
      return {
        success: false,
        totalAmt: 0,
        name: undefined,
        phone: undefined,
        email: undefined,
        location: undefined,
        time: undefined,
        fieldss: {},
        boneless_quantity: 0,
        bonein_quantity: 0,
        ribs_quantity: 0,
        mince_quantity: 0,
        steak_quantity: 0,
        steakless_quantity: 0,
        wele_quantity: 0,
        off_cut: undefined,
        offcut_quantity: 0,
        offal: undefined,
        offal_quantity: 0,
        errorMsg: undefined,
        errorSub: undefined
      }
    },
    methods: {
      async fetchData() {
        const response = await Devless.queryData('farmable', 'prices')
        if (response.status_code === 609) {
          this.fieldss = response.payload
          return
        }
        alert('An error occurred. Please reload')
      },
      validateBeforeSubmit() {
        this.$validator.validateAll().then((result) => {
          if (result) {
            // eslint-disable-next-line
            this.dataSumbit();
            return;
          }

          alert('Please correct the errors highlighted in red');
        });
      },
      async dataSumbit() {
        const response = await Devless.addData('farmable', 'orders', {
          "delivery_location": this.location,
          "offal_part": this.offal,
          "offal": this.offal_quantity,
          "off_cut_part": this.off_cut,
          "off_cut": this.offcut_quantity,
          "wele": this.wele_quantity,
          "steak_bone_in": this.steak_quantity,
          "steak_bonless": this.steakless_quantity,
          "mince": this.mince_quantity,
          "ribs": this.ribs_quantity,
          "bone_in_stewing_beef": this.bonein_quantity,
          "boneless_stewing_beef": this.boneless_quantity,
          "delivery_time": this.time,
          "email": this.email,
          "number": this.phone,
          "name": this.name,
          "offal_price": "",
          "off_cut_price": "",
          "wele_price": "",
          "steak_bone_in_price": "",
          "steak_bonless_price": "",
          "mince_price": "",
          "ribs_price": "",
          "bone_in_stewing_beef_price": "",
          "boneless_stewing_beef_price": "",
          "total_payable": "",
          "total_quantity": "",
          "created_at": ""
        })
        console.log(response)
        if (response.status_code === 609) {
          this.success = true
          this.errorMsg = undefined
          setTimeout(() => {
            window.open(response.payload, '_blank');
          }, 5000);
        } else {
          this.success = false
          this.errorMsg = response.message
          if (response.payload.internalMessage) {
            this.errorSub = response.payload.internalMessage
          }
        }
        window.scroll({
          top: 0,
          left: 0,
          behavior: 'smooth'
        });
      },
      // changeState() {
      //   this.$emit('change', this.totalAmt);
      // }
    },
    created() {
      this.fetchData()
    },
    computed: {
      total() {
        this.totalAmt = (this.fieldss.boneless_stewing_beef * this.boneless_quantity) + (this.fieldss.bone_in_stewing_beef * this.bonein_quantity)
          + (this.fieldss.ribs * this.ribs_quantity) + (this.fieldss.mince * this.mince_quantity) + (this.fieldss.steak_bone_in * this.steak_quantity)
          + (this.fieldss.steak_bonless * this.steakless_quantity) + (this.fieldss.wele * this.wele_quantity) + (this.fieldss.off_cut * this.offcut_quantity)
          + (this.fieldss.offal * this.offal_quantity)
        return this.totalAmt
      },
      offal_total() {
        return this.offal_quantity * this.fieldss.offal;
      }
    }
  }
</script>


