<template>
  <div class="container">
		<form class="payWrap">
			<div class="row">
				<div class="col-md-4 col-sm-6 col-xs-10 col-xs-offset-1 col-sm-offset-3 col-md-offset-4 payCont">
					<h3 class="text-center">Make Payment</h3>
					<hr>
					<div class="row text-center payLogo" style="margin-bottom: 30px">
						<div class="col-xs-4"><img class="img-responsive" src="../assets/mtn_m.jpg" alt=""></div>
						<div class="col-xs-4"><img class="img-responsive" src="../assets/tigo_m.jpg" alt=""></div>
						<div class="col-xs-4"><img class="img-responsive" src="../assets/airtel_m.jpg" alt=""></div>
					</div>
					<div class="row">
						<div class="form-group col-md-12">
							<select class="form-control mr-t-30" required="required" v-model="selected">
								<option disabled value="">Select Mobile Wallet</option>
								<option value="MTN">MTN Mobile Money</option>
								<option value="TIG">Tigo Cash</option>
								<option value="AIR">Airtel Money</option>
							</select>
						</div>
						<div class="form-group col-md-12">
							<label for="" class="control-label">Phone #.</label>
							<input type="text" class="form-control" id="" placeholder="Enter your wallet #" v-model="wallet">
						</div>
						<div class="form-group col-md-12" v-show="totalAmount">
							<label for="" class="control-label">Amount Payable GH&#x20B5;</label>
							<input type="text" class="form-control" :value="totalAmount" disabled>
						</div>
						<div class="form-group col-md-12 mr-t-20 text-center mr-b-0">							
							<button type="submit" class="btn btn-lg btn-primary">Make Payment</button>
						</div>
					</div>
				</div>
			</div>
		</form>
	</div>
</template>

<script>
  import Devless from '../utils/devless'

  export default {
    props: ['totalAmount'],
    data () {
      return {
        selected: '',
        wallet: ''
      }
    },
    methods: {
      async initiatePayment() {
        const res = await Devless.call('demorgan', 'makePayment', [this.selected, this.wallet, 1])
        console.log(res);
      }
    },
    mounted() {
      console.log('value coming thru', this.totalAmount)
    }
  }
</script>

<style scoped>
	.payWrap{
		height: 100vh;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.payLogo img {
		border-radius: 3px;
	}
	.payCont {
		border: 1px solid #aaa;
		padding: 30px;
		border-radius: 5px;
		background: #fff;
	}
</style>


