<template>
  <div>
    
      
    <order-view></order-view>
    <!--<payment-view v-else v-bind:totalAmount="totalAmount"></payment-view>-->
        
  </div>
</template>

<script type="text/javascript">
  import OrderView from './OrderView'
  import PaymentView from './PaymentView'

  export default {
    components: {
      OrderView,
      PaymentView
    },
    data () {
      return {
        totalAmount: 0,
        state: true
      }
    },
    methods: {
      changeState (value) {
        this.state = false;
        this.totalAmount = value;
      }
    }
  }
</script>

<style>
  .is-danger {
    color: red;
  }
</style>

<style src="../assets/form.css"></style>


